import React, { Component } from 'react';
import { Button, Card, CardBody, CardFooter, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import { register } from '../../Pages/UserFuctions'
import '../Login/Login.scss';


class Register extends Component {
  constructor(props) {
    super(props);
    this.state = { 
        username: '',
        email: '',
        password: '',
        message: ''
    };

    this.onSubmit = this.onSubmit.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  onChange(e){
    this.setState({[e.target.name]: e.target.value});
  }

  onSubmit(e){
    e.preventDefault();

    const user = {
      username: this.state.username,
      email: this.state.email,
      password: this.state.password
    }

    register(user).then(res => {
      if(res){
        this.setState({message : res.message, isLoaded : true})
        console.log([this.state.message])
        if([this.state.message] == 'Email already exists'){
          this.props.history.push('/register')
        }
        else{
          this.props.history.push('/login')
        }
      }else{
        this.props.history.push('/register')
      }
    })
  }
  render() {
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="9" lg="7" xl="6">
              <Card className="mx-4">
                <CardBody className="p-4">
                  <h6 className='erro-msg'>{this.state.message}</h6>
                  <Form>
                    <h4>Register</h4>
                    <p className="text-muted">Create your account</p>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-user"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" name='username' placeholder="Username" autoComplete="username" onChange={this.onChange}/>
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>@</InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" name='email' placeholder="Email" autoComplete="email" onChange={this.onChange}/>
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="password" name='password' placeholder="Password" autoComplete="new-password" onChange={this.onChange}/>
                    </InputGroup>
                    <Button color="success" block onClick={this.onSubmit}>Create Account</Button>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Register;
